.footer__container {
  @include flex;
  position: relative;
  margin-inline: 0;
  max-width: 100%;
  padding: 37px 32px 40px;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  background-color: var(--bg-section);
}

.footer__btn-scroll {
  position: absolute;
  top: -88px;
  right: 8px;
  background-color: var(--bg-section);
  z-index: 1000;
}

.footer__btn-scroll svg {
  transform: rotateX(180deg);
}

.footer__left {
  min-width: min(701px, 50%);
  margin-right: 20px;
}

.footer .nav__link {
  @include text-title-uppercase;
}

.footer__logo {
  margin-bottom: 48px;
}

.footer__right {
  width: min(42%, 495px);
}

.contacts__item {
  @include text-descr;
  @include mb(2px);
  color: #45556b;
}

.contacts__link {
  @include text-descr;
  display: block;
  transition: color var(--transition-3), transform var(--transition-3);
}

.contacts__link:focus {
  outline: none;
}

.contacts__link:hover,
.contacts__link:focus-visible {
  color: var(--blue);
  transform: translateY(-2px);
}

.contacts__link:active {
  color: var(--blue-dark);
  transform: translateY(-2px);
}
