// glop
@include tablet {
  html {
    font-size: 13px;
  }

  section .section-label {
    font-size: clamp(20px, 4vw, 32px);
    line-height: clamp(24px, 5vw, 43px);
  }
}

// header
@include small-tablet {
  .nav__list {
    display: none;
  }

  .burger {
    display: block;
  }
}

// hero
// 1024
@include tablet {
  section .hero__label {
    font-size: clamp(24px, 5vw, 32px);
    line-height: clamp(27px, 7vw, 43px);
  }

  .commercial {
    @include flex-col;
    @include content-center;
    padding-top: 20px;
  }

  .commercial__bottom {
    @include flex-col;
    @include content-center;
  }

  .commercial__img-wrap {
    margin-bottom: 0;
  }
}

// 480
@include small-mobile {
  .hero__text {
    grid-column: span 2;
  }

  .hero__commercial {
    grid-row: auto;
    grid-column: span 2;
  }

  .commercial__text {
    font-size: 12px;
  }

  .hero__right {
    grid-row: auto;
    grid-column: span 2;
  }

  .hero__right img {
    object-position: center -23px;
  }
}



// advantages
// 1024
@include tablet {
  section .advantages__label {
    font-size: clamp(16px, 4vw, 32px);
    line-height: clamp(24px, 5vw, 43px);
  }
}

@include small-tablet {
  .advantages__title {
    font-size: 12px;
    margin-bottom: 12px;
  }

  .advantages__content-wrap {
    padding-top: 28px;
    padding-bottom: 48px;
  }

  .advantages__list {
    grid-template-columns: repeat(2, auto);
    grid-gap: 10px;
  }

  .advantages__box {
    @include flex-col;
    text-align: center;
  }

  .advantages__box-img {
    margin-right: 0;
  }

}



// filer
// 768
@include small-tablet {
  .filer__item {
    width: calc((100% - 20px) / 2);
  }

  .filer__left {
    display: none;
  }
}



// params
// 1024
@include tablet {
  .params__left {
    padding: 28px;
  }
}

// 768
@include small-tablet {
  .params__top {
    @include flex-col;
  }

  .params__left {
    max-width: 100%;
    margin-right: 0;
    margin-bottom: 20px;
  }

  .params__btn {
    max-width: 300px;
    margin-inline: auto;
  }

  .params .params-ac .params-ac__btn {
    padding-block: 20px;
    padding-left: 24px;
  }

  .params .params-ac .ac-panel {
    padding-inline: 24px;
  }

  .params-ac__img-wrap_lc {
    display: none;
  }

  .params-ac_1 .params-ac__list {
    margin-right: 0;
  }

  .params-ac_2 .params-ac__item {
    @include flex-col;
  }

  .params .params-ac:not(:last-child) {
    margin-bottom: 12px;
  }

  .params-ac__text-wrap {
    margin-bottom: 16px;
  }

  .params-ac__sub-title {
    font-size: 14px;
  }

  .params-ac__number {
    height: 32px;
    min-width: 32px;
    max-width: 32px;
  }
}

// 576
@include mobile {

  .params__text {
    @include flex-col;
  }

  .params-ac_2 .params__text {
    @include flex-col;
  }

  .params .params-ac .params-ac__btn {
    padding-block: 14px;
    padding-left: 18px;
  }

  .params .params-ac .ac-panel {
    padding-inline: 18px;
  }

  .params-ac__sub-title {
    min-width: auto;
  }
}

// 480
@include small-mobile {

  .params-ac__images-wrap_1 {
    @include flex-col;
  }

  .params-ac__images-wrap_1 .params-ac__img-wrap:first-child {
    margin-right: 0;
  }
}


// research
// 1024
@include tablet {
  .research__title {
    font-size: 12px;
    margin-bottom: 12px;
  }

  .research__big-img {
    grid-row: auto;
  }

  .research__review {
    grid-column: 2;
  }

  .research__stat_small {
    grid-row: 2;
    grid-column: 1;
  }

  .research__stat-content_flex {
    @include flex-col;
    align-items: flex-start;
  }

  .research__stat-contents {
    @include flex-col;
  }

  .research__stat-content {
    margin-bottom: 12px;
  }
}

// 768
@include small-tablet {
  .research__label-wrap {
    @include flex-col;
    align-items: flex-start;
  }

  .research__label {
    margin-bottom: 16px;
  }

  .research__university {
    max-width: 110px;
  }

  .research__review {
    grid-column: 1/3;
  }

  .research__stat_small {
    grid-row: auto;
    grid-column: 1/3;
  }

  .research__box {
    padding: 24px 20px;
  }
}

// 576
@include mobile {

  .research__big-img {
    grid-column: 1/3;
  }

  .research__img-wrap {
    left: 50%;
    transform: translateX(-50%);
  }

  .research__stat_big {
    grid-column: 1/3;
  }

  .research__review-name {
    font-size: 14px;
  }

  .research__review-descr {
    font-size: 12px;
  }
}



// reviews
// 1024
@include tablet {

  .reviews__content-wrap {
    padding: 48px 28px;
  }

  .reviews__top {
    @include flex-col;
    align-items: flex-start;
  }

  .reviews__label {
    margin-bottom: 16px;
  }

  .box-review {
    padding: 16px;
  }

}

// 768
@include small-tablet {
  .reviews__content-wrap {
    padding: 24px 16px;
  }

  .reviews__box {
    padding: 16px;
    grid-column: span 3;
  }

  .reviews__box:first-child {
    grid-column: 1/7;
  }

  .reviews__box:last-child {
    grid-column: 1/7;
  }
}

// 480
@include small-mobile {
  .reviews__box:nth-last-child(-n+3) {
    display: none;
  }

  .reviews__box {
    grid-column: span 6;
  }
}



// address
// 768
@include small-tablet {

  .address__bottom {
    flex-direction: column-reverse;
  }

  .address__list-wrap {
    display: none;
  }

  .address__map-wrap {
    min-width: 100%;
    max-width: 100%;
    height: 600px;
  }
}