.address {
  margin-bottom: 104px;
}

.address a {
  transition: color var(--transition-2), transform var(--transition-2);
}

.address a:focus {
  outline: none;
}

.address a:hover,
.address a:focus-visible {
  color: var(--blue);
  transform: translateY(-2px);
}

.address a:active {
  color: var(--blue-dark);
  transform: translateY(-2px);
}

.address__label {
  max-width: 700px
}

.address__bottom {
  @include flex;
  min-height: 575px;
}

.address__map-wrap {
  position: relative;
  min-width: min(701px, 50%);
  max-width: min(701px, 50%);
  height: auto;
  margin-right: 20px;
  overflow: hidden;
}

.address__map {
  position: absolute;
  inset: 0;
}

.address__right {
  width: 100%;
}

.address__search {
  position: relative;
  margin-bottom: 19px;
}

.search__input {
  width: 100%;
  min-height: 56px;
  padding-inline: 48px;
  padding-top: 24px;
  padding-bottom: 8px;
  border-radius: 8px;
  border: 1px solid #c2c3cc;
  transition: border-color var(--transition-2);
}

.search__input:focus {
  outline: none;
}

.search:hover .search__input,
.search__input:focus {
  border-color: var(--blue);
}

.search__placeholder {
  @include text-descr;
  position: absolute;
  top: 50%;
  left: 48px;
  transform: translateY(-50%);
  color: #838383;
  transition: transform var(--transition-2), font-size var(--transition-2);
}

.search__input.value-full+.search__placeholder,
.search__input:focus+.search__placeholder {
  transform: translateY(-25px);
  font-size: 10px;
}

.search__icon-search {
  @include flex;
  position: absolute;
  left: 19px;
  top: 50%;
  transform: translateY(-50%);
}

.search__icon-search svg {
  height: 18px;
  width: 18px;
}

.search__icon-search use {
  fill: #160b75;
}

.search__cleaner {
  display: none;
  position: absolute;
  right: 21px;
  top: 50%;
  transform: translateY(-50%);
}

.search__cleaner.active {
  @include flex;
}

.search__cleaner svg {
  height: 13px;
  width: 13px;
}

.search__cleaner use {
  fill: var(--light-text);
  fill-opacity: 0.6;
  transition: fill var(--transition-2), fill-opacity var(--transition-2);
}

.search__cleaner:focus {
  outline: none;
}

.search__cleaner:hover use,
.search__cleaner:focus-visible use {
  fill-opacity: 1;
}

.search__cleaner:active use {
  fill: var(--dark-text);
  fill-opacity: 1;
}

.search__results {
  position: absolute;
  display: none;
  left: 0;
  top: 56px;
  width: 100%;
  padding-block: 8px;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 2px 10px 0 rgba($color: #000, $alpha: 0.1);
  animation: fadeInUp 0.4s ease-in-out;
  z-index: 1000;
}

.search__results.active {
  display: block;
}

.search__results-item {
  @include text-descr;
  padding: 10px;
  color: #000;
  transition: background-color var(--transition-2);
  cursor: pointer;
}

.search__results-item {
  outline: none;
}

.search__results-item:focus,
.search__results-item:hover {
  background-color: var(--lilac-light);
}

.search__results-item:active {
  background-color: var(--lilac-dark);
}

.address__list-wrap {
  width: 100%;
  max-height: 500px;
  padding-right: 12px;
}

.address__list {
  width: 100%;
}

.address__item:not(:last-child) {
  margin-bottom: 8px;
}

.box-address {
  padding: 16px;
  border-radius: 8px;
  border: 3px solid var(--bg-section);
  background-color: var(--bg-section);
  transition: border-color var(--transition-2);
  cursor: pointer;
}

.box-address:focus {
  outline-color: var(--lilac);
}

.box-address.active {
  border-color: var(--blue);
}

.box-address__title {
  margin-bottom: 12px;
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  color: var(--blue);
}

.box-address__address {
  @include text-descr;
  display: inline-block;
  margin-bottom: 16px;
  color: var(--dark-text);
}

.box-address__list {
  @include content-between;
}

.box-address__item {
  display: inline-block;
  padding-left: 24px;
  background-size: 16px;
  background-position: 0 4px;
  background-repeat: no-repeat;
}

.box-address__item_clock {
  background-image: url('../img/wall-clock.svg');
}

.box-address__item_phone {
  background-image: url('../img/phone-call.svg');
}

.box-address__text {
  @include text-descr;
  display: block;
  color: var(--dark-text);
}

.box-address__text:not(:last-child) {
  margin-bottom: 4px;
}


// map yandex
.cluster-map {
  @include flex;
  position: relative;
  height: 30px;
  width: 30px;
}

.cluster-map__number {
  @include flex-center-center;
  position: absolute;
  top: -6px;
  right: -6px;
  height: 18px;
  width: 18px;
  border-radius: 100%;
  border: 1px solid var(--blue);
  font-size: 12px;
  background-color: #fff;
  z-index: 10;
}

.address__map .box-address {
  // min-width: 293px;
  border: none;
  background-color: #fff;
  cursor: initial;
}

.address__map .box-address__title {
  font-size: 16px;
}

.address__map .box-address__item {
  margin-bottom: 16px;
}

.address__map .box-address__item-title {
  margin-bottom: 8px;
  padding-top: 3px;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  color: #778cad;
}

.address__map .box-address__address,
.address__map .box-address__text {
  font-size: 14px;
  line-height: 20px;
}

.address__map .box-address__list {
  @include flex-col;
}

ymaps[class*="balloon"] {
  margin: 0 !important;
  padding: 0 !important;
  border: none !important;
  border-radius: 8px !important;
  box-shadow: none !important;
}

ymaps[class*="balloon__close"] {
  position: absolute;
  right: 10px;
  top: 10px;
}

ymaps[class$="balloon__close-button"] {
  position: initial;
  height: 12px;
  width: 12px;
  transition: opacity var(--transition-2);
}

ymaps[class$="balloon__close-button"]:focus {
  outline: none;
}

ymaps[class$="balloon__close-button"]:hover,
ymaps[class$="balloon__close-button"]:focus-visible {
  opacity: 0.7;
}

ymaps[class$="balloon__close-button"]:active {
  opacity: 1;
}

ymaps[class*="balloon__close"]+ymaps[class*="balloon__content"] {
  margin: 0 !important;
  padding: 0 !important;
}

ymaps[class*="balloon_layout_normal"] {
  top: 25px !important;
  left: -131px !important;
  padding: 0 !important;
}

ymaps[class$="balloon__layout"] {
  box-shadow: 0 2px 10px 0 rgba($color: #000, $alpha: 0.1) !important;
}

ymaps[class$="balloon__tail"] {
  bottom: 295px;
  left: 50% !important;
  transform: translateX(-50%) rotateZ(45deg) !important;
}

.simplebar-content-wrapper {
  border-radius: 8px;
  outline-color: var(--blue) !important;
}

.address .js-focus-visible :focus:not(.focus-visible) {
  outline: initial;
}

.simplebar-scrollbar::before {
  width: 6px;
  background-color: var(--blue);
}

.simplebar-scrollbar.simplebar-visible::before {
  opacity: 1 !important;
}