.params__top {
  @include flex;
  margin-bottom: 24px;
}

.params__left {
  width: 100%;
  max-width: 598px;
  min-width: clamp(300px, 50vw, 500px);
  margin-right: 20px;
  padding: 56px;
  border-radius: 16px;
  background-color: var(--bg-section);
}

.params__list {
  margin-bottom: 40px;
}

.params__item:not(:last-child) {
  margin-bottom: 12px;
}

.params__text {
  @include flex-start-sb;
  border-bottom: 1px dotted rgba(#000, 0.2)
}

.params__key {
  @include text-descr;
  min-width: 220px;
  color: var(--light-text);
}

.params__value {
  text-align: end;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  color: var(--dark-text);
}

.params__value li:not(:last-child) {
  margin-bottom: 8px;
}

.params__value-lighter {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.params__btn {
  width: 100%;
}

.params__right {
  @include flex-center-center;
  @include flex-col;
  width: 100%;
  padding: 20px;
  border-radius: 16px;
  background-color: var(--light-violet);
}

.params__img-top-wrap {
  margin-bottom: 28px;
}

.params .params-ac {
  margin: 0;
  border: none;
  border-radius: 16px;
  background-color: var(--bg-section);
  overflow: hidden;
}

.params .params-ac:not(:last-child) {
  margin-bottom: 24px;
}

.params .params-ac .params-ac__btn {
  position: relative;
  font-size: clamp(13px, 3vw, 24px);
  line-height: clamp(18px, 3vw, 31px);
  font-weight: 600;
  padding: 28px 32px;
  padding-right: 80px;
  color: var(--blue);
  text-transform: uppercase;
  background-color: transparent;
  transition: background-color var(--transition-3);
}

.ac.is-active>.ac-header .ac-trigger::after,
.params .params-ac .params-ac__btn::after {
  display: none;
}

.params .params-ac .params-ac__btn::before {
  content: '';
  position: absolute;
  top: 50%;
  right: 33px;
  height: 22px;
  width: clamp(16px, 3vw, 22px);
  background-image: url('../img/plus.svg');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  transform: translateY(-50%);
  transition: background-image var(--transition-3);
}

.params .params-ac.is-active>.ac-header .params-ac__btn::before {
  background-image: url('../img/minus.svg');
}

.params .params-ac .params-ac__btn:focus {
  outline: none;
}

.params .params-ac .params-ac__btn:hover,
.params .params-ac .params-ac__btn:focus-visible {
  background-color: var(--lilac-light);
}

.params .params-ac .params-ac__btn:active {
  background-color: var(--lilac-dark);
}

.params .params-ac .ac-panel {
  padding-inline: 28px;
}

.params .ac-panel>.params-ac__text {
  margin-bottom: 16px;
}

.params-ac__text {
  @include text-descr;
  color: var(--light-text);
}

.params-ac__list-wrap {
  @include flex;
  margin-bottom: 32px;
  border-radius: 16px;
}

.params-ac__list {
  width: 100%;
  margin-right: 16px;
}

.params-ac__item {
  @include flex;
  padding: 16px;
  border-radius: 16px;
  background-color: #fff;
}

.params-ac__item:not(:last-child) {
  margin-bottom: 16px;
}

.params-ac__number {
  @include flex-center-center;
  height: 44px;
  min-width: 44px;
  max-width: 44px;
  margin-right: 16px;
  border: 1px solid var(--blue);
  border-radius: 100%;
  color: var(--blue);
}

.params-ac__text-wrap {
  @include items-center;
  width: 100%;
  margin-right: 16px;
}

.params-ac__sub-title {
  max-width: min(340px, 40vw);
  min-width: 340px;
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
  color: var(--blue);
}

.params-ac__images-wrap {
  @include flex;
}

.params-ac__images-wrap:not(:last-child) {
  margin-right: 16px;
}

.params-ac__img-wrap_lc {
  min-width: 362px;
  max-width: 362px;
  border-radius: 16px;
}

.params-ac__img-wrap_lc img {
  height: 100%;
}

.params-ac_1 .params-ac__item {
  align-items: center;
}

.params-ac_2 .params-ac__item {
  align-items: flex-start;
}