html {
  box-sizing: border-box;
  font-size: 16px;
  // scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

svg path {
  fill: inherit;
  fill-opacity: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

.site-container {
  max-width: var(--container-width);
  margin-inline: auto;
  background-color: #fff;
  color: var(--dark-text);
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  max-width: var(--container-width);
  padding-inline: clamp(12px, 2vw, var(--container-offset));
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}


// default-blocks
section {
  margin-bottom: clamp(20px, 5vw, 104px);
}

.img-wrap {
  @include flex;
  overflow: hidden;
}

.img-content {
  object-fit: contain;
}

.section-label {
  margin-bottom: min(32px, 2vw);
  font-family: 'Raleway';
  font-size: 40px;
  font-weight: 600;
  line-height: 50px;
  text-transform: uppercase;
  color: var(--blue);
}

.btn {
  @include flex-center-center;
  padding: 8px 24px;
  border: 1px solid var(--blue);
  border-radius: 30px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-transform: uppercase;
  color: var(--blue);
  transition: color var(--transition-3), background-color var(--transition-3);
}

.btn:focus {
  outline: none;
}

.btn:hover,
.btn:focus-visible {
  color: #fff;
  background-color: var(--blue);
}

.btn:active {
  color: #fff;
  background-color: var(--blue-dark);
}

.btn-scroll {
  @include flex-center-center;
  height: 56px;
  width: 56px;
  border-radius: 100%;
  background-color: rgba($color: #ffffffd6, $alpha: .6);
  backdrop-filter: blur(10px);
  transition: background-color var(--transition-3);
}

.btn-scroll__icon {
  height: 18px;
  width: 19px;
}

.btn-scroll__icon use {
  fill: var(--blue);
  transition: fill var(--transition-3);
}

.btn-scroll:hover {
  background-color: var(--blue);
}

.btn-scroll:focus-visible {
  outline: none;
  background-color: var(--blue);
}

.btn-scroll:active {
  background-color: var(--blue-dark);
}

.btn-scroll:hover use,
.btn-scroll:focus use,
.btn-scroll:active use {
  fill: #fff;
}


// margin
.mr-16 {
  margin-right: 16px;
}

.mr-24 {
  margin-right: 24px;
}

.mb-8 {
  margin-bottom: 8px;
}

.mb-12 {
  margin-bottom: 12px;
}

.mb-16 {
  margin-bottom: 16px;
}