@mixin flex {
  display: flex;
}

@mixin items-center {
  display: flex;
  align-items: center;
}

@mixin content-center {
  display: flex;
  justify-content: center;
}

@mixin content-between {
  display: flex;
  justify-content: space-between;
}

@mixin content-end {
  display: flex;
  justify-content: flex-end;
}

@mixin flex-center-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin flex-center-sb {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@mixin flex-start-sb {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

@mixin flex-col {
  display: flex;
  flex-direction: column;
}

@mixin flex-wrap {
  flex-wrap: wrap;
}