.reviews__content-wrap {
  padding: 72px 48px;
  border-radius: 16px;
  background-color: var(--bg-section);
}

.reviews__top {
  @include flex-center-sb;
  margin-bottom: 32px;
}

.reviews__label {
  margin-right: 48px;
  margin-bottom: 0;
}

.market {
  @include items-center;
}

.market__item:not(:last-child) {
  margin-right: 32px;
}

.market__link {
  @include items-center
}

.market__number {
  font-size: 20px;
  line-height: 28px;
  font-weight: 500;
  margin-right: 8px;
  color: var(--dark-text);
}

.market__star {
  margin-right: 8px;
}

.market__img-wrap {
  max-width: clamp(30px, 2vw, 40px);
  border-radius: 10px;
  transition: filter var(--transition-3);
}

.market__link:focus {
  outline: none;
}

.market__link:hover .market__img-wrap,
.market__link:focus-visible .market__img-wrap {
  filter: hue-rotate(20deg);
}

.market__link:active .market__img-wrap {
  filter: hue-rotate(40deg);
}

.reviews__boxes {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: auto;
  gap: 16px;
}

.reviews__box {
  @include flex-col;
  grid-column: span 2;
}

.reviews__box:nth-child(4n) {
  grid-column: 1/7;
}

.box-review {
  padding: 24px;
  border-radius: 16px;
  border: 1px solid #e4e4e4;
  background-color: #fff;
}

.box-review__text {
  @include text-descr;
  margin-bottom: 16px;
  color: var(--dark-text)
}

.box-review__bottom {
  @include flex;
  margin-top: auto;
}

.box-review__avatar {
  display: block;
  height: 40px;
  width: 40px;
  border-radius: 100%;
  margin-right: 12px;
  background-color: var(--light-violet);
}

.box-review__name {
  @include text-descr;
  display: block;
  margin-bottom: 4px;
  font-weight: 500;
  color: var(--dark-text)
}

.box-review__star {
  display: inline-block;
}

.box-review__star:not(:last-child) {
  margin-right: 4px;
}