.advantages {
  margin-bottom: 24px;
}

.advantages__content-wrap {
  @include flex-col;
  @include items-center;
  width: 100%;
  padding-inline: 24px;
  padding-top: 56px;
  padding-bottom: 72px;
  border-radius: 16px;
  background-color: var(--bg-section);
}

.advantages__title {
  margin-bottom: 24px;
  @include text-title-uppercase;
}

.advantages__label {
  text-align: center;
  font-size: 32px;
  line-height: 43px;
}

.advantages__description {
  @include text-descr;
  margin-bottom: 40px;
  max-width: 607px;
  text-align: center;
}

.advantages__list {
  display: grid;
  grid-template-columns: repeat(3, min(calc((100% - 32px) / 3), 279px));
  gap: 16px;
  max-width: 845px;
}

.advantages__box {
  @include items-center;
}

.advantages__box-img {
  display: inline-block;
  height: 60px;
  width: 60px;
  margin-right: 20px;
  background-size: contain;
  background-repeat: no-repeat;
}

.advantages__box-img_eyes {
  background-image: url('../img/eye.svg')
}

.advantages__box-img_blob {
  background-image: url('../img/blob.svg')
}

.advantages__box-img_penetration {
  background-image: url('../img/penetration.svg')
}

.advantages__box-img_def {
  background-image: url('../img/def.svg')
}

.advantages__box-img_standard {
  background-image: url('../img/standard.svg')
}

.advantages__box-img_patent {
  background-image: url('../img/patent.svg')
}

.advantages__box-text {
  @include text-descr;
  color: var(--light-text);
}