.header,
.header-fixed {
  padding-block: clamp(16px, 3%, 28px);
  background-color: #fff;
}

.header__container {
  @include flex-center-sb;
}

.logo {
  @include flex;
  max-width: min(40vw, 206px);
}

.nav__list {
  @include flex;
}

.nav__item:not(:last-child) {
  margin-right: 40px;
}

.nav__link {
  position: relative;
  display: inline-block;
  font-size: 1rem;
  font-weight: 500;
  line-height: 24px;
  text-transform: uppercase;
  color: var(--dark-text);
  transition: color var(--transition-3);
}

.nav__link::before {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 50%;
  height: 2px;
  width: 0;
  background-color: var(--blue);
  transform: translateX(-50%);
  transition: width var(--transition-3), background-color var(--transition-3);
}

.nav__link:focus {
  outline: none;
}

.nav__link:hover,
.nav__link:focus-visible {
  color: var(--blue);
}

.nav__link:hover::before,
.nav__link:focus-visible::before {
  width: 100%;
}

.nav__link:active {
  color: var(--blue-dark);
}

.nav__link:active::before {
  width: 100%;
  background-color: var(--blue-dark);
}


// header-fixed
.header-fixed {
  position: fixed;
  display: none;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 2px 10px 0 rgba($color: #000, $alpha: 0.1);
  transform: translateY(-100%);
  z-index: 10000;
}

.header-fixed.active {
  display: block;
}
