/* stylelint-disable */

@mixin burger {
  --burger-width: 40px;
  --burger-height: 40px;
  --burger-line-height: 2px;

  position: relative;
  border: none;
  border-radius: 100%;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: var(--blue);
  background-color: transparent;
  z-index: 10001;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 11px;
    right: 11px;
    height: var(--burger-line-height);
    border-radius: 12px;
    background-color: currentColor;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  }

  &::before {
    top: 14px;
  }

  &::after {
    bottom: 14px;
  }

  &__line {
    position: absolute;
    left: 11px;
    right: 11px;
    top: 50%;
    height: var(--burger-line-height);
    background-color: currentColor;
    transform: translateY(-50%);
    transition: transform 0.3s ease-in-out;
  }

  &--active {
    &::before {
      top: 50%;
      transform: rotate(45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::after {
      top: 50%;
      transform: rotate(-45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__line {
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }
  }
}
