// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Inter", "Raleway", sans-serif;
  --site-width: 1280px;
  --content-width: 1216px;
  --container-offset: 32px;
  --container-width: calc(var(--content-width) + var(--container-offset)*2);
  --transition-3: 0.3s ease-in-out;
  --transition-2: 0.2s ease-in-out;

  // colors
  --blue: #160b75;
  --blue-dark: #0d0551;
  --bg-section: #f8f8f8;
  --light-text: #697688;
  --dark-text: #3a3a46;
  --light-blue: #e0f6ff;
  --light-violet: #e0edff;
  --lilac: #a097ec;
  --lilac-light: rgba(160, 151, 236, 0.15);
  --lilac-dark: rgba(160, 151, 236, 0.40);
}
