.hero__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto clamp(292px, 40vw, 336px);
  grid-gap: min(20px, 2%);
  grid-row-gap: min(52px, 4vw);
}

.hero__left,
.hero__right {
  width: calc(50% - 10px);
}

.hero__left {
  @include flex-col;
  @include content-end;
  margin-right: 20px;
}

.hero__text {
  @include flex-col;
  @include content-end;
}

.hero__label {
  font-size: clamp(24px, 5vw, 48px);
  line-height: clamp(32px, 7vw, 57px);
}

.hero__title {
  @include text-descr;
  color: var(--dark-text);
}

.commercial {
  max-height: 336px;
  padding: 16px;
  padding-top: 44px;
  border-radius: 16px;
  background-color: var(--light-blue);
}

.commercial__img-wrap {
  max-width: clamp(190px, 40vw, 368px);
  margin-bottom: 31px;
  margin-inline: auto;
}

.commercial__bottom {
  @include flex-center-sb;
}

.commercial__text {
  @include text-title-uppercase;
  color: var(--dark-text);
  text-transform: uppercase;
}

.hero__right {
  position: relative;
  grid-row: span 2;
  max-height: 751px;
  width: 100%;
  height: 100%;
  border-radius: 24px;
}

.hero__right picture {
  width: 100%;
}

.hero__right img {
  height: 100%;
  width: 100%;
}

.hero__img-girl {
  object-fit: cover;
}

.hero__btn-scroll {
  position: absolute;
  right: 39px;
  bottom: 24px;
}

.hero__btn-scroll::before {
  content: 'scroll down';
  position: absolute;
  top: -20px;
  left: 50%;
  min-width: 100px;
  font-size: 12px;
  line-height: 18px;
  font-weight: 400;
  text-transform: uppercase;
  color: #fff;
  transform: translateX(-50%);
  transition: transform var(--transition-3);
}

.hero__btn-scroll:hover::before,
.hero__btn-scroll:focus::before {
  transform: translate(-50%, -10px);
}