.filer {
  @include flex;
  margin-bottom: 24px;
}

.filer__item {
  @include flex-center-sb;
  height: min(345px, 30vw);
  width: calc((100% - 40px) / 3);
  border-radius: 16px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.filer__item:not(:last-child) {
  margin-right: 20px;
}

.filer__left {
  background-image: url('../img/lense-on-hand.jpg');
}

.filer__center {
  background-image: url('../img/lense-on-table.jpg');
}

.filer__right {
  background-image: url('../img/lense-eye.jpg');
}