.research__title {
  @include text-title-uppercase;
  margin-bottom: 24px;
}

.research__label-wrap {
  @include flex-center-sb;
  margin-bottom: 32px;
}

.research__label {
  max-width: 900px;
  margin-bottom: 0;
  margin-right: 20px;
}

.research__content-wrap {
  display: grid;
  grid-template-columns: auto clamp(2px, 55vw, 700px);
  grid-template-rows: auto auto;
  row-gap: 16px;
  column-gap: 20px;
}

.research__big-img {
  position: relative;
  grid-row: span 3;
  min-height: 200px;
  border-radius: 16px;
  @include image-set('../img/girls-smile');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.research__img-wrap {
  @include flex-center-center;
  position: absolute;
  left: 16px;
  bottom: 16px;
  width: clamp(150px, 33vw, 370px);
  padding: 20px;
  border-radius: 16px;
  background-color: #ffffffd6;
  backdrop-filter: blur(10px);
}

.research__box {
  padding: 32px 28px;
  border-radius: 16px;
}

.research__stat {
  background-color: var(--bg-section);
}

.research__stat-text {
  @include text-descr;
  color: var(--light-text);
}

.research__stat-contents {
  @include flex;
}

.research__stat-content_flex {
  @include items-center;
}

.research__number {
  margin-bottom: 8px;
  font-size: clamp(24px, 5vw, 48px);
  line-height: clamp(32px, 7vw, 58px);
  font-weight: 600;
  color: var(--blue);
}

.research__review {
  background-color: var(--light-blue);
}

.research__review-text {
  @include text-descr;
  position: relative;
  margin-bottom: 16px;
  padding-left: 18px;
  color: var(--dark-text);
}

.research__review-text::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0px;
  width: 2px;
  border-radius: 4px;
  background-color: var(--blue);
}

.research__review-bottom {
  @include flex;
}

.research__review-img-wrap {
  height: 55px;
  min-width: 55px;
  max-width: 55px;
  margin-right: 16px;
  border-radius: 100%;
}

.research__review-img {
  object-position: 0 -6px;
}

.research__review-name {
  display: inline-block;
  margin-bottom: 4px;
  font-size: 16px;
  line-height: 26px;
  font-weight: 600;
  color: var(--blue);
}

.research__review-descr {
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  color: var(--light-text);
}