.burger {
  @include burger;
  // position: fixed;
  // top: 4px;
  // right: 14px;
  display: none;
  transition: background-color var(--transition-2);
  outline: none;
}

.burger:hover {
  background-color: var(--lilac-light);
}

.burger:focus,
.burger:active {
  background-color: var(--lilac-dark);
}

.burger-menu {
  position: fixed;
  inset: 0;
  display: none;
  background-color: rgba($color: #000, $alpha: 0.5);
  z-index: 10000;
}

.burger-menu.menu--active {
  display: block;
}

.burger-menu .nav {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  max-width: 400px;
  padding: 32px;
  padding-top: 105px;
  background-color: #fff;
}

.burger-menu .nav__list {
  @include flex-col;
}

.burger-menu .nav__item {
  @include mb(16px);
}

.burger-menu .nav__link {
  font-size: 16px;
  @include mb(16px);
}
